// Hashtag state
export default class HashState {
    static get() {
        return new Set((location.hash || '')
            .replace(/^#/, '')
            .split(',')
            .map(x => x.replace(/^\s+|\s+$/g, ''))
            .filter(x => x.length));
    }
    static set(input) {
        location.hash = Array.from(input).join(',');
    }
}



